.navbar{
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
}
.navbar ul{
    margin: 0;
    padding: 0;
}
.navbar ul li{
    list-style: none;
    display: inline-block;
}

.navbar ul li a{
    text-decoration: none;
    display: block;
    text-transform: capitalize;
    font-weight: 400;
    color: #565673; 
    padding: 0 10px;  
}

.search form{
    display: flex;
    margin: 0 10px;
}

.searchinput{
    border: 0;
    width: 0;
    transition: width 0.1s ease-in;

}

.searchinput.active{
    width: 150px;
    border: 1px solid #444;
}

.searchinput:focus{
    outline: none;

}

.searchicon{
    cursor: pointer;
}